<template lang="pug">
  div.swiper-slide
    div.swiper-container#app_swiper2
      div.swiper-wrapper
        swiperPageTwoone
        swiperPageTwotwo
        swiperPageTwothree
        swiperPageTwofour
        swiperPageTwofive
</template>

<script>
import swiperPageTwoone from './pagetwo-one.vue';
import swiperPageTwotwo from './pagetwo-two.vue';
import swiperPageTwothree from './pagetwo-three.vue';
import swiperPageTwofour from './pagetwo-four.vue';
import swiperPageTwofive from './pagetwo-five.vue';
// import swiper from 'swiper'

export default {
  name: 'page2'
  ,
  data() {
    return {
      secSwiperIndex: 0
    }
  }
  ,
  components: {
    swiperPageTwoone,
    swiperPageTwotwo,
    swiperPageTwothree,
    swiperPageTwofour,
    swiperPageTwofive
  }
  // ,
  // computed: {
  //     astring: function () {
  //       return 33333
  //   }
  // }
  ,
  mounted() {
    let vm = this;
    let swiper = vm.$root.swiper;
    const appMainSwiper2 = new swiper('#app_swiper2', {
      direction: 'horizontal',
      onlyExternal: true
    });
    vm.$root.swipers.appMainSwiper2 = appMainSwiper2;

    appMainSwiper2.on('slideChangeEnd', function(swiper) {
      vm.$data.secSwiperIndex=swiper.activeIndex;
    });

    vm.$watch('secSwiperIndex', function(newVal, oldVal) {
      if (newVal === 1) {
        vm.$root.swipers.appMainSwiper.lockSwipeToPrev()
      }
      if (newVal === 2) {
        setTimeout(()=> {
          appMainSwiper2.slideNext();
        }, 1000)
      } else {
        vm.$root.swipers.appMainSwiper.unlockSwipeToPrev()
      }
    });
    // mounted END --
  }
}
</script>

<style scoped lang="less">
  .swiper-container {
    height: 100%;
  }
</style>



