<template lang="pug">
  div.swiper-slide
    div.title1
      img(src='~assets/images/title-1.png')
    div.page-banner
      div.page-banner-inner
        img.bgsize(src='~assets/images/page-1-size.png')
        img.layer01(src='~assets/images/page-limg01.png')
        img.layer02(src='~assets/images/page-limg02.png')

    div.jjj_block
      div.jjj1
        img(src='~assets/images/jjj-1.png')
      div.jjj2
        img(src='~assets/images/jjj-2.png')
      div.jjj3
        img(src='~assets/images/jjj-3.png')

    div.page-bot
      div.inner
        img(src='~assets/images/page-limg-bot.png')
</template>

<script>
export default {
  name: 'page1'

}
</script>

<style scoped lang="less">
  .jjj_block {
    // border: solid 3px red;
    position: absolute;
    top: 49.6%;
    width: 100%;
    box-sizing: border-box;
    .jjjcss() {
      & > img {
        vertical-align: top;
        width: 100%;
      }
    }
    .jjj1 {
      width: 117% / 320 * 100;
      // height: 107px;
      position: absolute;
      left: 7%;
      background-size: contain;
      .jjjcss()
    }
    .jjj2 {
      width: 92% / 320 * 100;
      // height: 107px;
      right: 7.5%;
      position: absolute;
      background-size: contain;
      .jjjcss()
    }
    .jjj3 {
      width: 97% / 320 * 100;
      // height: 117px;
      top: 79px / 2;
      left:38.3%;
      position: absolute;
      background-size: contain;
      .jjjcss()
    }
  }
  .swiper-slide {
    position: relative;
    background-repeat: no-repeat;
    background-position: center top;
    background-color: #000;
    background-image: url('~assets/images/page-1-bgc.jpg');
    background-size: 100%;
  }
  .title1 {
    img {
      width: 100%;
      vertical-align: top;
    }
  }
  @keyframes pbopacity {
    0% { opacity: 0.3}
    100% {opacity: 1}
  }
  .page-bot {
    position: absolute;
    bottom: 25px;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    // border: solid 2px red;
    & > .inner {
      display: inline-block;
      width: 76.25%;
      & > img {
        width: 100%;
        vertical-align: top;
        animation: pbopacity 2s linear infinite;
        animation-direction: alternate-reverse;
      }
    }
  }
  .page-banner {
    margin: 0 auto;
    text-align: center;
  }
  .page-banner-inner {
    position: relative;
    display: inline-block;
    width: 90.1%;
    .bgsize {
      vertical-align: top;
      width: 100%;
    }
    .layerallcss() {
      vertical-align: top;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
    .layer01 {
      .layerallcss()
    }
    .layer02 {
      .layerallcss()
    }
    .layer03 {
      .layerallcss()
    }
    .layer04 {
      .layerallcss()
    }
    .layer05 {
      .layerallcss()
    }
  }
</style>



