<template lang="pug">
  div.swiper-slide
    div.page-banner
      div.page-banner-inner
        img.bgsize(src='~assets/images/page-2-size.png')
        img.layer01(src='~assets/images/page2-limg01.png')
        img.layer02(src='~assets/images/page2-limg02.png')
        img.layer02(src='~assets/images/page2-limg03.png')

    //- 1
    div.ckjjj1
      div.text
        img(src='~assets/images/page2-ckjjj-text-1.png', height='37')
      div.pic
        img(src='~assets/images/page2-ckjjj1.jpg')
      div.grow-1
      div.grow-2
    //- 2
    div.ckjjj2
      div.text
        img(src='~assets/images/page2-ckjjj-text-2.png', height='39')
      div.pic
        img(src='~assets/images/page2-ckjjj2.jpg')
      div.grow-1
      div.grow-2
    //- 3
    div.ckjjj3
      div.text
        img(src='~assets/images/page2-ckjjj-text-3.png', height='38')
      div.pic
        img(src='~assets/images/page2-ckjjj3.jpg')
      div.grow-1
      div.grow-2

    div.page-bot
      div.inner
        img(src='~assets/images/page-limg-bot2.png')
</template>

<script>
export default {
  name: 'page2-1'
  ,
  data() {
      return {

      }
  }
  ,
  mounted() {
    let vm = this;
    setTimeout(()=> {
      let $ = vm.$root.$;
      let mainSwipper = vm.$root.swipers.appMainSwiper;
      let secSwiper = vm.$root.swipers.appMainSwiper2;
      console.log(mainSwipper,secSwiper);
      // secSwiper.on('slideChangeEnd', function(swiper) {
      //   vm.$data.testObj=swiper.activeIndex;
      // })

      $('.ckjjj1, .ckjjj2, .ckjjj3').off('.ckj');
      $('.ckjjj1, .ckjjj2, .ckjjj3').on('click.ckj', function() {
        secSwiper.slideNext();
      })
    },0)
    //mounted END
  }
  ,
  methods: {}
}
</script>

<style scoped lang="less">
.ckjjjcss() {
  width: 121px;
  height: 121px;
  position: absolute;
  & > * {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom:  0;
    margin: auto;
  }
  & > .text {
    width: 100%;
    left: 0;
    right: auto;
    top: auto;
    bottom: 100%;
    text-align: center;
    img {
      vertical-align: top;
    }
  }
  & > .grow-1 {
    width: 121px;
    height: 121px;
    background-image: url('~assets/images/grow-1-1.png');
    background-position: right center;
    background-size: auto 121px;

    animation: runani 15s linear;
    animation-iteration-count: infinite;
    animation-direction: reverse;
  }
  &:active > .grow-1 {
    background-position: left center;
  }
  & > .grow-2 {
    width: 94px;
    height: 94px;
    background-size: cover;
    animation: runani 10s linear;
    animation-iteration-count: infinite;
  }
  & > .pic {
    width: 165px / 2;
    height: 165px / 2;
    border-radius: 1000px;
    overflow: hidden;
    img {
      vertical-align: top;
      width: 100%;
    }
  }
}

// 
@keyframes runani {
  0% {
    transform: rotate(0deg);
    opacity: 1;
  }
  25% {
    opacity: 0.55;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.55;
  }
  100% {
  opacity: 1;
    transform: rotate(360deg);
  }
}
.ckjjj1 {
  top: 56.15%;
  left: 7.65625%;
  .ckjjjcss();
  & > .grow-2 {
    background-image: url('~assets/images/grow-2-1.png');
  }
}
.ckjjj2 {
  left: 50%;
  top: 40.15%;
  margin-left: 121px / -2;
  .ckjjjcss();
  & > .grow-2 {
    background-image: url('~assets/images/grow-2-2.png');
  }
}
.ckjjj3 {
    top: 56.15%;
    right: 7.65625%;
  .ckjjjcss();
  & > .grow-2 {
    background-image: url('~assets/images/grow-2-3.png');
  }
}


.swiper-slide {
  position: relative;
  background-repeat: no-repeat;
  background-position: center top;
  background-color: #000;
  background-image: url('~assets/images/page-2-bgc.jpg');
  background-size: 100%;
}

.page-bot {
  position: absolute;
  bottom: 25px;
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  // border: solid 2px red;
  & > .inner {
    display: inline-block;
    width: 69%;
    & > img {
      width: 100%;
      vertical-align: top;
      animation: pbopacity 2s linear infinite;
      animation-direction: alternate-reverse;
    }
  }
}
.page-banner {
  margin: 0 auto;
  text-align: center;
  margin-top: 2.1%;
}
.page-banner-inner {
  position: relative;
  display: inline-block;
  width: 93.125%;
  .bgsize {
    vertical-align: top;
    width: 100%;
  }
  .layerallcss() {
    vertical-align: top;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .layer01 {
    animation:nihong1 2s linear infinite;
    animation-direction: alternate-reverse;
    .layerallcss()
  }
  .layer02 {
    animation:nihong1 2s 1s linear infinite;
    animation-direction: alternate-reverse;
    .layerallcss()
  }
  .layer03 {
    animation: pbopacity2 3s linear infinite;
    animation-direction: alternate-reverse;
    .layerallcss()
  }
  .layer04 {
    .layerallcss()
  }
  .layer05 {
    .layerallcss()
  }
}
</style>



