<template lang="pug">
  -var imageURL = '~assets/images/page5/'
  -var imageURL3 = '~assets/images/page3/'
  div.swiper-slide
    div.title1
      div.inner
        img.inimg1(src= imageURL3 + 'title-2.png')
        img.inimg2(src= imageURL3 + 'title-1.png')
    div.fftt
      img(src= imageURL + "fontt01.png", height='89')
      div.fts
        input.ftsinp.ftsinp1
        input.ftsinp.ftsinp2
        input.ftsinp.ftsinp3
    div.page-bot
      div.inner
        img.botbgi(src= imageURL3 + 'bot-1.png')
        div.nextBtn(v-on:click="showModal")
    div.maskss(v-if='showmaskss', v-on:click='showmaskss = false')
</template>

<script>
  export default {
    name: 'page2-5'
    ,
    data() {
        return {
          showmaskss:false
        }
    }
    ,
    mounted() {
      let vm = this;
      // setTimeout(()=> {
      //   let $ = vm.$root.$;
      //   let mainSwipper = vm.$root.swipers.appMainSwiper;
      //   let secSwiper = vm.$root.swipers.appMainSwiper2;
      //   secSwiper.slideNext();
      // },1000)
      //mounted END
    }
    ,
    methods: {
      showModal:function() {
        this.showmaskss = true
      }
    }
  }
</script>

<style scoped lang="less">
  .title1 {
    position: absolute;
    left: 0;
    top: 44% / 1008 * 100;
    width: 100%;
    text-align: center;
    & > .inner {
      display: inline-block;
      width: 75%;
      position: relative;
      & > img {
        // position: absolute;
        // left: 0;
        // top: 0;
        width: 100%;
        vertical-align: top;
        &.inimg2 {
          animation:nihong1 2s 1s linear infinite;
          animation-direction: alternate-reverse;
          position: absolute;
          left: 0;
          top: 0;
        }
      }
    }
  }

  .page-bot {
    position: absolute;
    bottom: 25px;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    // border: solid 2px red;
    & > .inner {
      display: inline-block;
      width: 55700% / 640;
      & > .botbgi {
        width: 100%;
        vertical-align: top;
        animation: pbopacity 2s linear infinite;
        animation-direction: alternate-reverse;
      }
      .nextBtn {
        position: absolute;
        z-index: 1;
        top: 37px;
        left: 50%;
        margin-left: 268px / -4;
        width: 268px /2;
        height:  63px /2;
        background-image: url('~assets/images/page5/subf.png');
        background-size: cover;
      }
    }
  }
  .swiper-slide {
    position: relative;
    background-repeat: no-repeat;
    background-position: center top;
    background-color: #000;
    background-image: url('~assets/images/page3/bgi.jpg');
    background-size: 100%;
  }
  .fftt {
    position: absolute;
    text-align: center;
    top: 16%;
    // background-color: #ffffff;
    width: 100%;
  }
  .ftsinp {
    width: 428px / 2;
    height: 94px /2;
    box-sizing: border-box;
    border: none;
    background: none;
    background-size: contain;
    padding-left: 80px;
    outline: none;
    color: #fff;
    text-shadow: 1px 1px 0 #000;
    margin-top: 20px;
    &.ftsinp1 {
      background-image: url('~assets/images/page5/inpts1.png');
    }
    &.ftsinp2 {
      background-image: url('~assets/images/page5/inpts2.png');
    }
    &.ftsinp3 {
      background-image: url('~assets/images/page5/inpts3.png');
    }
  }
  .maskss {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: url('~assets/images/page5/maskss.png');
    background-size: cover;
    z-index: 999;
  }
</style>



