<template lang="pug">
  div#app
    div.swiper-container#app_swiper
      div.swiper-wrapper
        pageone
        pagetwo
    transition(name='fade')
      div(v-if='swiperCount < 1').swiperHepler
</template>

<script>
  import pageone from './components/pageone'
  import pagetwo from './components/pagetwo'
  import $ from 'jquery'
  import swiper from 'swiper'
  import attachFastClick from 'fastclick'
  // import * as gsap from 'gsap'

  // let appSwipperPromise = function(swiper) {
  //   return new Promise(function(resolve, reject) {
  //
  //   });
  // }

  export default {
    name: 'app',
    created() {
      // Cache
      this.$root.$ = $;
      this.$root.swiper = swiper;
      this.$root.swipers = {};
      // this.$root.tweenMax = gsap.tweenMax;
    },
    data() {
      return {
        swiperCount: 0
      }
    },
    mounted() {
      let vm = this;
      attachFastClick.attach(document.body);
      const appMainSwiper = new swiper('#app_swiper', {
        direction: 'vertical'
      });
      vm.$root.swipers.appMainSwiper = appMainSwiper;
      appMainSwiper.on('slideChangeEnd', function(swiper) {
        vm.$data.swiperCount=swiper.activeIndex;
      })

    },
    components: {
      pageone,
      pagetwo
    }
  }
</script>

<style lang="less">
  @import (less) "./assets/css/normalize.css";
  @import (less) "./assets/css/swiper.min.css";

  html,body {
    height: 100%;
    background-color: #000;
  }


  // CSS
  @keyframes pbopacity {
    0% { opacity: 0.3}
    100% {opacity: 1}
  }
  @keyframes pbopacity2 {
    0% { opacity: 0.6}
    100% {opacity: 1}
  }

  // css
  @keyframes nihong1 {
    0% { opacity: 1}
    40% {opacity: 0.65}
    90% {opacity: 1}

    92% {opacity: 1  }
    93% {opacity: 0.65}
    94% {opacity: 1  }

    95% {opacity: 1  }
    96% {opacity: 0.65}
    97% {opacity: 1  }

    98% {opacity: 1  }
    99% {opacity: 0.65}
    100% {opacity: 1  }
  }

  // 
  @keyframes runani {
    0% {
      transform: rotate(0deg);
      opacity: 1;
    }
    25% {
      opacity: 0.55;
    }
    50% {
      opacity: 1;
    }
    75% {
      opacity: 0.55;
    }
    100% {
    opacity: 1;
      transform: rotate(360deg);
    }
  }



  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .5s
  }
  .fade-enter,
  .fade-leave-active {
    opacity: 0
  }
  #app {
    font-family: -apple-system, "PingFang SC", "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
  }
  #app_swiper {
    height: 100%;
  }
  .swiper-slide:not(.swiper-slide-active) * {
    animation-play-state: paused !important;
  }
  @keyframes swiperHeplerAni {
    0% {transform: translateY(-10px);}
    100% {transform: translateY(0px);}
  }
  .swiperHepler {
    position: fixed;
    bottom: 10px;
    left: 50%;
    margin-left:  30px / -4;
    width: 30px / 2;
    height: 29px / 2;
    background-image: url('~assets/images/swiperhelper.png');
    background-size: contain;
    animation: swiperHeplerAni 1s linear infinite;
    animation-direction: alternate-reverse;
    z-index: 999;
  }
</style>



