<template lang="pug">
  -var imageURL = '~assets/images/page5/'
  div.swiper-slide
    div.fourCors
      .tr
        img(width=29,src=imageURL + 'red-stars.png')
      .tl
        img(width=29,src=imageURL + 'red-stars.png')
      .br
        img(width=29,src=imageURL + 'red-stars.png')
      .bl
        img(width=29,src=imageURL + 'red-stars.png')
    div.centeregg
      div.eggAnimatFrame(v-on:click= "gonext", :class= '{startAnimate : isAnimate, endAnimate: endAnimate}')

    div.ps.p1(v-if="thepn === 1")
      img(src= imageURL + 'p1.png')
    div.ps.p2(v-if="thepn === 2")
      img(src= imageURL + 'p2.png')
    div.ps.p3(v-if="thepn === 3")
      img(src= imageURL + 'p3.png')

    div.btns
      transition(name="fade")
        div.btnss.btn1(v-if="endAnimate" ,v-on:click="showthisModal")
          img(src= imageURL + 'btn1.png', height='30')
      transition(name="fade")
        div.btnss.btn2(v-if="endAnimate && thepn < 3" , v-on:click="getps")
          img(src= imageURL + 'btn2.png', height='30')
      transition(name="fade")
        div.btnss.btn3(v-if="endAnimate", v-on:click="resetegg")
          img(src= imageURL + 'btn3.png', height='30')
    div.massk01(v-if="showthismodalstr === 1")
      div.massk01-inner
        p
          |NEWA RF   AAkWWWsNhttp://e22a.com/h.1yS9Sv?cv=AAkWWWsN&sm=c4bd97
      div.clsbtn(v-on:click="showthismodalstr = 0")
    div.massk02(v-if="showthismodalstr === 2 || showthismodalstr === 3")
      img(src= imageURL + "gzhewm.png")
      div.clsbtn(v-on:click="showthismodalstr = 0")
</template>

<script>
  import $ from 'jquery';

  export default {
    name: 'page2-4'
    ,
    data() {
        return {
          showthismodalstr: 0,
          isAnimate: false,
          endAnimate:false,
          thepn :0
        }
    }
    ,
    mounted() {
      let vm = this;
      // setTimeout(()=> {
      //   let $ = vm.$root.$;
      //   let mainSwipper = vm.$root.swipers.appMainSwiper;
      //   let secSwiper = vm.$root.swipers.appMainSwiper2;
      //   secSwiper.slideNext();
      // },1000)
      //mounted END
    }
    ,
    methods: {
      gonext : function(event) {
        let _this = this;
        $(event.target).one('animationend webkitAnimationEnd', function() {
          _this.endAnimate = true;
          _this.isAnimate = false;
          let rd = Math.random() * 10;
          if ( rd <= 3 ) {_this.thepn = 1}
          if ( rd > 3 && rd <= 6 ) {_this.thepn = 2}
          if ( rd > 6 && rd <= 9 ) {_this.thepn = 3}
          if ( rd > 9 ) {_this.thepn = 3}
          console.log(_this.thepn);


          $(event.target).fadeOut('300', function() {

          });

        });
        _this.isAnimate = true;
      },
      resetegg: function () {
        let _this = this;
        $('.eggAnimatFrame').fadeIn('300', function() {});
        _this.thepn = 0;
        _this.endAnimate = false;
        _this.isAnimate = false;
      },
      getps:function() {
        this.$root.swipers.appMainSwiper2.slideNext();
      },
      showthisModal: function() {
        let vmobj = this;
        if (vmobj.thepn === 1) { vmobj.showthismodalstr = 1}
        if (vmobj.thepn === 2) { vmobj.showthismodalstr = 2}
        if (vmobj.thepn === 3) { vmobj.showthismodalstr = 3}
      }
    }
  }
</script>

<style scoped lang="less">
  .fourCors {
    position: static;
    // left: 0;
    // top: 0;
    .trcss() {
      position: absolute;
    }
    .tr {
      top: 10px;
      right: 10px;
      .trcss();
    }
    .tl {
      top: 10px;
      left: 10px;
      .trcss();
    }
    .br {
      bottom: 10px;
      right: 10px;
      .trcss();
    }
    .bl {
      bottom: 10px;
      left: 10px;
      .trcss();
    }
  }


  @keyframes aniegg {

    100%  {background-position: (563px / 2 * -5) 0%;}
  }
  .ps {
    position: absolute;
    top: 5%;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 80%;
    // margin-top: 10%;
    & img {
      width: 100%;
      vertical-align: top;
    }
  }
  .centeregg {
    width: 563px / 2;
    height: 865px / 2;
    margin-left: 563px / -4;
    margin-top: 865px / -4;
    position: absolute;
    top: 50%;
    left: 50%;
    // background-color: #ffffff;
    .eggAnimatFrame {
      width: 100%;
      height: 100%;
      background-image: url('~assets/images/page5/egganis.png');
      background-size: auto 100%;
      &.startAnimate {
        animation: aniegg 1s linear;
        animation-fill-mode: both;
        animation-timing-function: steps(5);
      }
      &.endAnimate {
        background-position: 100% top;

      }
    }
  }

  .fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}


  .swiper-slide {
    position: relative;
    background-repeat: no-repeat;
    background-position: center top;
    background-color: #000;
    background-image: url('~assets/images/page3/bgi.jpg');
    background-size: 100%;
  }
  .btns {
    position: absolute;
    bottom: 20px;
    width: 100%;
    text-align: center;
    .btnss {
      display: inline-block;
    }
  }

  .massk02 {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    & > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .clsbtn {
      position: absolute;
      width: 19px;
      height: 19px;
      top: 20px;
      right: 20px;
      background-image: url('~assets/images/page5/clsbtn2.png');
      background-size: cover;
    }
  }

  .massk01 {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-position: center bottom;
    background-image: url('~assets/images/page5/massk01.png');
    background-size: cover;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .massk01-inner {
      width: 531px /2 ;
      height:  346px / 2;
      text-align: center;
      background-image: url('~assets/images/page5/massk01-text-border.png');
      background-size: 100%;
      color: #b2f0ff;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      p {
        margin: 0 20px;
      }
    }
    .clsbtn {
      position: absolute;
      width: 19px;
      height: 19px;
      top: 20px;
      right: 20px;
      background-image: url('~assets/images/page5/clsbtn.png');
      background-size: cover;
    }
  }
</style>



